import React, { Component } from 'react';
import './App.css';
import EventList from './EventList';
import CitySearch from './CitySearch';
import NumberOfEvents from './NumberOfEvents';
// import { mockData } from './mock-data';
import { extractLocations, getEvents } from './api';
import "./nprogress.css";

class App extends Component {

  state = {
    events: [],
    locations: [],
    numberOfEvents: 32
  }

  componentDidMount() {
    this.mounted = true;

    getEvents().then((events) => {
      const eventCount = events.slice(0, this.state.numberOfEvents)

      if (this.mounted) {
        this.setState({ events: eventCount , locations: extractLocations(events) });
        // console.log(eventCount.length)
        // console.log(this.state.numberOfEvents)
      }
    });

    // numberOfEvegggnts().then((events)=>{
    //   const eventCount = events.slice(0, this.state.numberOfEvents)

    //   if (this.mounted) {

    //     this.setState({ events: eventCount, locations: extractLocations(events) });
    //   }
    // })

  }

  componentWillUnmount(){
    this.mounted = false;
  }

  updateEvents = (location, numberOfEvents) => {
    getEvents().then((events) => {
      
      
      const locationEvents = (location === 'all') ?
        events :
        events.filter((event) => event.location === location);

          


          getEvents().then((events) => {
            const eventCount = (numberOfEvents === 32) ? 
        events :
        events.slice(0, numberOfEvents);

        if(numberOfEvents ===32){
          this.setState({
            events: locationEvents,
            numberOfEvents: 32
          })
        }
        else{
          this.setState({
            events: eventCount,
          })
        }
      
        

          })



    });
    console.log("mekdda")
  }


  




  render() {
    
    return (
      <div className="App">
        <CitySearch locations={this.state.locations} updateEvents={this.updateEvents} />
        <EventList events={this.state.events}/>
        <NumberOfEvents updateEvents={this.updateEvents} numberOfEvents={this.state.numberOfEvents} locations={this.state.locations}/>
      </div>
    );
  }
}

export default App;